<style scoped lang="scss">
//base
@import "../theme/theme.scss";
* {
    /*outline: 1px solid #eee;*/
}

.article {
    padding-top: 35px;
    position: relative;
    .article-detail {}
    .article-aside {
        position: fixed;
        /*padding-left: 20px;*/
        width: 370px;
        box-sizing: border-box;
        color: #333;
        .topBar {
            width: 100%;
            /*border: 1px solid transparent;*/
            margin-bottom: 15px;
            box-sizing: border-box;
            .topBar--title {
                padding: 18px 0 8px;
                background: rgba(0, 0, 0, 0.5);
                .topBar--title__h3 {
                    border-left: 5px solid $base-theme-color;
                    padding: 0 0 0 10px;
                    margin: 0;
                    color: #fff;
                    margin-left: 10px;
                    @include display-flex;
                    @include justify-content(flex-start);
                    @include align-items(center);
                    transition: all ease 200ms;
                    small {
                        color: $base-theme-color;
                        margin-left: 7px;
                    }
                }
            }
            .topBar--ul {
                padding: 8px 20px;
                background: #fff;
                list-style-type: none;
                position: relative;
                min-height: 150px;
                margin: 0;
                .topBar--loading {
                    width: 100%;
                    height: 100%;
                    display: flex;
                    justify-content: center;
                    align-items: center;
                    min-height: 135px;
                }
                .topArticle--li {
                    color: #777;
                    position: relative;
                    padding: 4px 0;
                    font-size: 14px;
                    &:before {
                        color: #ccc;
                        content: "\f0da";
                        font-size: 12px;
                        margin-right: 6px;
                        font-family: FontAwesome;
                        transition: all ease 200ms;
                    }
                    a {
                        color: inherit;
                        text-decoration: none;
                        transition: all ease 200ms;
                    }
                    span {
                        cursor: pointer;
                        transition: all ease 200ms;
                    }
                    &:hover {
                        color: $base-theme-color;
                        a {
                            color: $base-theme-color;
                        }
                        &:before {
                            color: $base-theme-color;
                        }
                        span {
                            color: $base-theme-color;
                        }
                    }
                }
                .topTag--li {
                    border: 1px solid #ccc;
                    display: inline-block;
                    margin: 4px;
                    color: #777;
                    padding: 2px 8px;
                    transition: all ease 200ms;
                    cursor: pointer;
                    a {
                        color: #777;
                        text-decoration: none;
                        transition: all ease 200ms;
                        span {
                            font-size: 12px;
                            color: #fff;
                            min-width: 10px;
                            padding: 1px 7px;
                            font-weight: bold;
                            line-height: 1;
                            white-space: nowrap;
                            text-align: center;
                            background-color: #969696;
                            border-radius: 10px;
                            transition: all ease 200ms;
                        }
                    }
                    &:hover {
                        color: $base-theme-color;
                        border-color: $base-theme-color;
                        a {
                            color: $base-theme-color;
                            span {
                                background-color: $base-theme-color;
                            }
                        }
                    }
                }
            }
        }
        .tagTop10 {
            width: 100%;
        }
    }
}

.article-detail {
    /*width: 780px;*/
    min-width: 740px;
    max-width: 980px;
    /*width: 52%;*/
    margin: 0 auto;
    color: $base-word-color;
    box-sizing: border-box;
    position: relative;
    z-index: 999;
    .paper {
        background-color: transparent;
        /*margin-bottom: 30px;*/
        /*border-radius: 4px;*/
        overflow: hidden;
        margin-bottom: 30px;
        &.loading {
            .paper__header {
                h1 {
                    background: #eee;
                }
            }
            .paper__info {
                .paper__info--span {
                    opacity: 0.3;
                    background: #fff;
                    min-width: 70px;
                    text-decoration: none;
                }
            }
            .paper__content {
                .paper__content--inner {
                    background-image: url(../assets/airoland-logo.png);
                    background-repeat: no-repeat;
                    background-size: 100% auto;
                    background-position: center top;
                    min-height: 834px;
                    width: 100%;
                }
            }
        }
        .paper__header {
            border: 1px solid transparent;
            padding: 35px 35px 0;
            background: #fff;
            text-align: right;
            a {
                text-decoration: none;
            }
            h1 {
                min-height: 52px;
                width: 100%;
                box-sizing: border-box;
                font-size: 36px;
                font-weight: 500;
                line-height: 1.2;
                margin: 20px 0 10px;
            }
        }
        .paper__info {
            @include display-flex;
            @include justify-content(flex-end);
            @include align-items(center);
            background: $base-background-color;
            padding: 10px 35px;
            box-shadow: 0 2px 2px 0 rgba(0, 0, 0, 0.8) inset;
            transition: all ease 200ms;
            .paper__info--span {
                color: #eee;
                margin-left: 20px;
                font-size: 14px; //min-width: 70px;
                white-space: nowrap;
                text-decoration: none;
            }
            a:hover {
                color: $base-theme-color;
            }
        }
        .paper__content {
            padding: 35px;
            background: #fff; //min-height: 800px;
            .paper__content--inner {
                padding: 5px 0 0; //border-bottom: 1px dashed #464646;
            }
        }
        .paper__navBox {
            background-color: #fff;
            .paper__navBox__inner {
                padding: 20px 0;
                text-align: center;
                list-style: none;
                color: $base-word-color; //border: 1px solid transparent;
                //padding: 35px;
                @include display-flex;
                @include justify-content(center);
                @include align-items(center);
                border-bottom: 1px solid #000;
                div {
                    margin: 0 10px; //border: 1px solid #464646;
                    padding: 5px 20px;
                    font-size: 16px; //border-radius: 28px;
                    //transition: all ease 200ms;
                    //cursor: pointer;
                    //&:hover {
                    //  color: #fff;
                    //  border-color: $base-theme-color;
                    //  background-color: $base-theme-color;
                    //}
                }
            }
        }
    }
}

.backToTop {
    position: fixed;
    left: 700px;
    width: 60px;
    height: 60px;
    background: rgba(0, 0, 0, 0.5);
    border-radius: 100%;
    font-size: 20px;
    color: #fff;
    display: flex;
    justify-content: center;
    align-items: center;
    cursor: pointer;
    transition: all ease 200ms;
    z-index: 999;
    margin-left: 40px;
    bottom: 20px;
    opacity: 0;
    &:hover {
        background: $base-theme-color;
    }
}

@include media("<=desktop") {
    .backToTop {
        margin-left: 0;
    }
}

@include media("<=desktop_small") {
    .article {
        padding-top: 60px;
    }
}

@include media("<=tablet") {
    .article-detail {
        max-width: 780px;
        min-width: inherit;
        width: auto;
    }
}

@include media("<=phone") {
    .article {
        padding-left: 6px;
        padding-right: 6px;
        padding-top: 51px;
        .row {
            margin: 0;
            #article {
                padding: 0;
            }
        }
        .article-detail {
            .paper {
                margin-bottom: 10px;
                .paper__header {
                    padding: 30px 10px 10px;
                    h1 {
                        font-size: 28px;
                        font-weight: 500;
                        line-height: 120%;
                        margin: 0;
                        min-height: inherit;
                    }
                }
                .paper__info {
                    display: flex;
                    justify-content: flex-end;
                    align-items: center;
                    padding: 8px 0px;
                    background: rgba(0, 0, 0, 0.5);
                    .paper__info--span {
                        margin: 0 10px;
                        font-size: 12px;
                    }
                }
                .paper__content {
                    padding: 20px 10px 20px;
                    .paper__content--inner {
                        font-size: 14px !important;
                    }
                }
            }
        }
    }
}
</style>

<template>
<div class="article animated fadeIn container">
    <div class="row">
        <div id="article" class="col-lg-8">
            <div class="article-detail">
                <!---->
                <div class="paper" :class="{'loading':isLoading}">
                    <section class="paper__header">
                        <ol class="breadcrumb  hidden-xs">
                            <li>
                                <router-link :to="{ name: 'index'}" tag="a"></router-link>
                            </li>
                            <li>
                                <router-link :to="{ name: 'blog'}" tag="a"></router-link>
                            </li>
                            <li class="active"></li>
                        </ol>
                        <h1>{{article.title}}</h1>
                    </section>
                    <section class="paper__info">
                        <div class="paper__info--span">
                            <i class="fa fa-calendar"></i>
                            <span>{{article.time}}</span>
                        </div>
                        <!--hidden-xs-->
                        <div class="paper__info--span hidden-xs" v-for="tag of article.tags">
                            <i class="fa fa-tag"></i> <span>{{tag}}</span>
                        </div>
                    </section>
                    <section class="paper__content">
                        <div class="paper__content--inner markdown-body hljs" v-html="article.html">
                            <!--{{{article.content}}}-->
                        </div>
                        <!--page-->
                    </section>

                    <!--the end-->
                </div>
            </div>
            <copyright></copyright>
        </div>
        <div class="col-lg-4 visible-lg clearfix">
            <aside class="article-aside">
                <!-- for 10-->
                <div class="topBar">
                    <div class="topBar--title">
                        <h3 class="topBar--title__h3">RECENT
                            <small>Top{{topNum}}</small>
                        </h3>
                    </div>
                    <ul class="topBar--ul">
                        <loading :number="3" color="#38b7ea" class="topBar--loading" v-if="!articleTop.recent"></loading>
                        <li v-show="articleTop.recent.length>0" class="topArticle--li animated fadeIn" v-for="article of articleTop.recent">
                            <router-link :to="{ name: 'article',params: { articleId: article.id }}" activeClass="active" tag="a">{{article.title}}
                            </router-link>
                        </li>
                    </ul>
                </div>
                <!-- for 10-->
                <div class="topBar">
                    <div class="topBar--title">
                        <h3 class="topBar--title__h3">RECOMMENDED
                            <small>Top{{topNum}}</small>
                        </h3>
                    </div>
                    <ul class="topBar--ul">
                        <loading :number="3" color="#38b7ea" class="topBar--loading" v-if="!articleTop.recent"></loading>
                        <li v-show="articleTop.recommended.length>0" class="topArticle--li animated fadeIn" v-for="article of articleTop.recommended">
                            <router-link :to="{ name: 'article',params: { articleId: article.id }}" activeClass="active" tag="a">{{article.title}}
                            </router-link>
                        </li>
                    </ul>
                </div>
                <!-- 10-->
                <div class="topBar">
                    <div class="topBar--title">
                        <h3 class="topBar--title__h3">TAGS
                            <!--<small>Top10</small>-->
                        </h3>
                    </div>
                    <ul class="topBar--ul">
                        <loading :number="3" color="#38b7ea" class="topBar--loading" v-if="!articleTop.recent"></loading>
                        <li v-show="articleTop.tags.length>0" class="topTag--li  animated fadeIn" v-for="tag of articleTop.tags">
                            <router-link :to="{ name: 'tagListFindByTagName',query: { listType: 'tagList',tagName: tag.name }}" activeClass="active" tag="a">
                                {{tag.name}}({{tag.used}})
                            </router-link>
                        </li>
                    </ul>
                </div>
            </aside>
        </div>
    </div>
    <!---->
    <div id="toTop" class="backToTop">
        <i class="fa fa-arrow-up"></i>
    </div>
</div>

</template>

<script type="text/javascript">
import API from "../config.js"
import {
    GetArticleById,
    GetArticleTop
} from "../api/api_article"
import loading from '../components/loading.vue'
import "../theme/codeHighLight.css";
import "../theme/markdown.scss";
import "bootstrap/scss/bootstrap/_breadcrumbs.scss";
import copyright from '../components/copyright.vue';
import Toast from 'Toast';
module.exports = {
    replace: true,
    data: function() {
        return {
            article: {},
            commentList: [],
            articleTop: {},
            toggle: true,
            selectId: '',
            isLoading: true,
            hasNickName: false,
            name: '', //
            email: '', //email
            topNum: 5, //top 
        }
    },
    watch: {
        '$route': function(val) {
            // 
            this.getArticleById(val.params.articleId);
        },
    },
    methods: {
        scrollTop: () => {
            $(window).scrollTop(0); // 
        },
        /**
        * 
        * @param articleId id
        * */
        getArticleById: function(articleId) {
            const _this = this;
            //
            GetArticleById(articleId).then(function(data) {
                _this.article = data;
                _this.isLoading = false;
                // title
                tools.changeTitle(_this.article.title + " - AIR");
                // aMarkDown
                setTimeout(function(){
                    $(".paper__content--inner.markdown-body a").attr("target","_blank");
                },1000)
            }, function(error) {
                if (error == 2) {
                    Toast({
                        message: '',
                        iconClass: 'fa fa-warning',
                        position: 'center',
                        duration: 3000
                    });

                    setTimeout(function() {
                        _this.$router.replace({
                            name: 'historyList'
                        })
                    }, 3000)
                }
            });
        },

        /**
            * 
            * @param topNum 
            * */
        getArticleTop: function(topNum) {
            const _this = this;
            /**
                *  userAgent mobiletop
                * */
            if (!navigator.userAgent.match(/AppleWebKit.*Mobile.*/)) {
                GetArticleTop(topNum).then(function(data) {
                    _this.articleTop = data;
                }, function(error) {
                    Toast({
                        message: '',
                        iconClass: 'fa fa-warning',
                        position: 'center',
                        duration: 3000
                    });
                });
            }
        },

        /**
            * handler
            * */
        backToTopHandler: function() {
            let _width = $(document).width()
            if (_width >= 1200) {
                if ($(this).scrollTop() > 0) {
                    $('#toTop').css({
                        "opacity": 1,
                        'left': $('#article').offset().left + $('#article').width(),
                    });
                } else {
                    $('#toTop').css({
                        "opacity": 0
                    });
                }
            } else if (_width < 1200) {
                if ($(this).scrollTop() > 0) {
                    $('#toTop').css({
                        "opacity": 1,
                        'left': $('#article').offset().left + $('#article').width() - $('#toTop').width(),
                    });
                } else {
                    $('#toTop').css({
                        "opacity": 0
                    });
                }
            }
        }
    },
    created: function() {
        const _this = this;
        let articleId = _this.$route.params.articleId;

        $(window).scrollTop(0);
        // To Top
        $(document)
            .on('scroll', _this.backToTopHandler)
            .on('click', '#toTop', function() {
                $(window).scrollTop(0);
                //$('body, html').animate({scrollTop: 0}, 600);
            });

        // 
        _this.getArticleById(articleId);
        // top
        _this.getArticleTop(_this.topNum);
    },
    mounted: function() {
        this.$emit('notice')
    },
    destroyed: function() {
        $(document).off('scroll')
    },
    components: {
        copyright,
        loading
    },
}
</script>


